<template>
  <img alt="Vue logo" src="./assets/logo.png">
  <HelloWorld msg="Counter App"/>
<div id="app">
  <button id="plus_rc_10" @keypress.prevent="" @click.prevent="countIt('rc', 'plus', 10)">+ 10 rc</button>
  <button id="plus_rc_7" @keypress.prevent="" @click.prevent="countIt('rc', 'plus', 7)">+ 7 rc</button>
  <button id="plus_rc_4" @keypress.prevent="" @click.prevent="countIt('rc', 'plus', 4)">+ 4 rc</button>
  <button id="plus_rc_2" @keypress.prevent="" @click.prevent="countIt('rc', 'plus', 2)">+ 2 rc</button>
  <button id="plus_rc_1" @keypress.prevent="" @click.prevent="countIt('rc', 'plus', 1)">+ 1 rc</button>
  <br>
  <div style="display:none">
    <button id="minus_rc_10" @keypress.prevent="" @click.prevent="countIt('rc', 'minus', 10)">- 10 rc</button>
    <button id="minus_rc_7" @keypress.prevent="" @click.prevent="countIt('rc', 'minus', 7)">- 7 rc</button>
    <button id="minus_rc_4" @keypress.prevent="" @click.prevent="countIt('rc', 'minus', 4)">- 4 rc</button>
    <button id="minus_rc_2" @keypress.prevent="" @click.prevent="countIt('rc', 'minus', 2)">- 2 rc</button>
    <button id="minus_rc_1" @keypress.prevent="" @click.prevent="countIt('rc', 'minus', 1)">- 1 rc</button>
  </div>
  <span id="rc">rc count: <span id="rc_count">{{ rc }}</span></span>
  <br><br>
  <button id="plus_other_10" @keypress.prevent="" @click.prevent="countIt('other', 'plus', 10)">+ 10 other</button>
  <button id="plus_other_7" @keypress.prevent="" @click.prevent="countIt('other', 'plus', 7)">+ 7 other</button>
  <button id="plus_other_4" @keypress.prevent="" @click.prevent="countIt('other', 'plus', 4)">+ 4 other</button>
  <button id="plus_other_2" @keypress.prevent="" @click.prevent="countIt('other', 'plus', 2)">+ 2 other</button>
  <button id="plus_other_1" @keypress.prevent="" @click.prevent="countIt('other', 'plus', 1)">+ 1 other</button>
  <br>
  <div style="display:none">
    <button id="minus_other_10" @keypress.prevent="" @click.prevent="countIt('other', 'minus', 10)">- 10 other</button>
    <button id="minus_other_7" @keypress.prevent="" @click.prevent="countIt('other', 'minus', 7)">- 7 other</button>
    <button id="minus_other_4" @keypress.prevent="" @click.prevent="countIt('other', 'minus', 4)">- 4 other</button>
    <button id="minus_other_2" @keypress.prevent="" @click.prevent="countIt('other', 'minus', 2)">- 2 other</button>
    <button id="minus_other_1" @keypress.prevent="" @click.prevent="countIt('other', 'minus', 1)">- 1 other</button>
  </div>
  <span id="other">other count: <span id="other_count">{{ other }}</span></span>
  <br><br>
  <button id="plus_torn_10" @keypress.prevent="" @click.prevent="countIt('torn', 'plus', 10)">+ 10 torn</button>
  <button id="plus_torn_7" @keypress.prevent="" @click.prevent="countIt('torn', 'plus', 7)">+ 7 torn</button>
  <button id="plus_torn_4" @keypress.prevent="" @click.prevent="countIt('torn', 'plus', 4)">+ 4 torn</button>
  <button id="plus_torn_2" @keypress.prevent="" @click.prevent="countIt('torn', 'plus', 2)">+ 2 torn</button>
  <button id="plus_torn_1" @keypress.prevent="" @click.prevent="countIt('torn', 'plus', 1)">+ 1 torn</button>
  <br>
  <div style="display:none">
    <button id="minus_torn_10" @keypress.prevent="" @click.prevent="countIt('torn', 'minus', 10)">- 10 torn</button>
    <button id="minus_torn_7" @keypress.prevent="" @click.prevent="countIt('torn', 'minus', 7)">- 7 torn</button>
    <button id="minus_torn_4" @keypress.prevent="" @click.prevent="countIt('torn', 'minus', 4)">- 4 torn</button>
    <button id="minus_torn_2" @keypress.prevent="" @click.prevent="countIt('torn', 'minus', 2)">- 2 torn</button>
    <button id="minus_torn_1" @keypress.prevent="" @click.prevent="countIt('torn', 'minus', 1)">- 1 torn</button>
  </div>
  <span id="torn">torn count: <span id="torn_count">{{ torn }}</span></span>
  <br><br>
  <button id="plus_kab_10" @keypress.prevent="" @click.prevent="countIt('kab', 'plus', 10)">+ 10 kab</button>
  <button id="plus_kab_7" @keypress.prevent="" @click.prevent="countIt('kab', 'plus', 7)">+ 7 kab</button>
  <button id="plus_kab_4" @keypress.prevent="" @click.prevent="countIt('kab', 'plus', 4)">+ 4 kab</button>
  <button id="plus_kab_2" @keypress.prevent="" @click.prevent="countIt('kab', 'plus', 2)">+ 2 kab</button>
  <button id="plus_kab_1" @keypress.prevent="" @click.prevent="countIt('kab', 'plus', 1)">+ 1 kab</button>
  <br>
  <div style="display:none">
    <button id="minus_kab_10" @keypress.prevent="" @click.prevent="countIt('kab', 'minus', 10)">- 10 kab</button>
    <button id="minus_kab_7" @keypress.prevent="" @click.prevent="countIt('kab', 'minus', 7)">- 7 kab</button>
    <button id="minus_kab_4" @keypress.prevent="" @click.prevent="countIt('kab', 'minus', 4)">- 4 kab</button>
    <button id="minus_kab_2" @keypress.prevent="" @click.prevent="countIt('kab', 'minus', 2)">- 2 kab</button>
    <button id="minus_kab_1" @keypress.prevent="" @click.prevent="countIt('kab', 'minus', 1)">- 1 kab</button>
  </div>
  <span id="kab">kab count: <span id="kab_count">{{ kab }}</span></span>
  <br><br>
  <div v-for="(history, index) in histories" :key="index">
    {{ history.txt }}<br>
  </div>
</div>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'

export default {
  name: 'App',
  components: {
    HelloWorld
  },
  data () {
    return {
      histories: [
        {txt: '1/31/2023 rc 5 other 20 torn 4 kab 0'},
        {txt: '1/30/2023 rc 3 other 33 torn 0 kab 1'}
      ],
      counters: ['rc', 'other', 'torn', 'kab'],
      rc: 0,
      other: 0,
      torn: 0,
      kab: 0
    }
  },
  mounted () {
    this.process_counts()
    this.create_history()
  },
  methods: {

    process_counts () {
      this.rc = 0
      this.other = 0
      this.torn = 0
      this.kab = 0
      const today = new Date().toLocaleDateString()
      for (const key in localStorage) {
        for (const x in this.counters) {
          const counter = this.counters[x]
          if (key.includes(counter)) {
            const keyBits = key.split('-')
            const dt = keyBits[1]
            const amountToAdd = parseInt(localStorage.getItem(key))
            if (dt.includes(today)) {
              this[counter] += amountToAdd
            } else {
              const historyKey = 'history_' + keyBits[0] + '-' + dt
              if (localStorage[historyKey] === undefined) {
                localStorage.setItem(historyKey, 0)
              }
              const thereBefore = parseInt(localStorage.getItem(historyKey))
              const newAmount = thereBefore + amountToAdd
              localStorage.setItem(historyKey, newAmount)
            }
          }
        }
      }
    },

    create_history() {
      var creatingHistory = []
      for (const key in localStorage) {
        const historyKey = 'history_'
        if (key.includes(historyKey)) {
          var hasDayForHistorySet = 0
          const lastBits = key.split('-')
          const whichDay = lastBits[1].split(',')
          const justTheDate = whichDay[0]
          const firstBits = lastBits[0].split('_')
          const typ = firstBits[1]
          for (const x in this.histories) {
            if (this.histories[x].txt.includes(justTheDate)) {
              hasDayForHistorySet = 1
            }
          }
          if (hasDayForHistorySet == 0 && creatingHistory[justTheDate] === undefined) {
            creatingHistory[justTheDate] = {}
          }
          for (const x in this.counters) {
            const counter = this.counters[x]
            if (creatingHistory[justTheDate][counter] === undefined) {
              creatingHistory[justTheDate][counter] = 0
            }
            if (typ === counter) {
              creatingHistory[justTheDate][counter] += parseInt(localStorage.getItem(key))
            }
          }
        }
      }
      for (const dt in creatingHistory) {
        var text = {txt: dt}
        const bits = creatingHistory[dt]
        for (const x in bits) {
          text.txt = text.txt + ' ' + x + ' ' + bits[x]
        }
        this.histories.push(text)
      }
    },

    countIt (which, typ, amount) {
      const now = new Date().toLocaleString([], {hour12: false})
      const key = which + '-' + now
      let toSaveDown = 0
      if (typ === 'plus') {
        toSaveDown += amount
      } else if (typ === 'minus') {
        toSaveDown -= amount
      }
      localStorage.setItem(key, toSaveDown)
      this.process_counts()
    }

  }
}
</script>

